.about__container{
    display:grid;
    grid-template-columns:35% 50%;
    gap:15%;
}

.about__me{
    width:75%;
    aspect-ratio:0.5/0.5;
    border-radius:2rem;
    background:linear-gradient(45deg,transparent,var(--color-primary),transparent);
    display:grid;
    place-items:center;

}


.about__me-image{
    width:300px;
    border-radius:2rem;
    overflow:hidden;
    transform: rotate(10deg);
    transition:var(--transition);
}

.about__me-image:hover{
    transform:rotate(0deg);
}

.about__cards{
    display:grid;
    grid-template-columns:repeat(3,1fr);
    gap:1.5rem;
}

.about__card{
    background:var(--color-bg-variant);
    border:1px solid transparent;
    border-radius: 4rem;
    padding:2rem;
    text-align: center;
    transition:var(--transition);

}
.about__card:hover{
        background:transparent;
        border-color:var(--color-primary-variant);
}

.about__icon{

    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom:1rem;
}

.about_card h5{
    font-size:0.95rem;
}

.about__card small{
    font-size:0.7rem;
    color:var(--color-light);

}

.about__content p{
    margin:2rem 0 2.6rem;
    color:var(--color-light);
}

/*---------------------MEDIA QUERIES(MEDIUM SCREEN DEVICES)===========*/
@media screen and (max-width:1024px) {


.responsive{
margin-top:25rem;
}

    .about__container {
        
        grid-template-columns: 2fr;
        gap:0;
    }
    .about__me{
        width:50%;
        margin:2rem auto 4rem;
    }

    .about__contnet p{
        margin:1rem 0 1.5rem;
    }
}

/*---------------------MEDIA QUERIES(SMALL SCREEN DEVICES)===========*/
@media screen and (max-width:600px) {

    .responsive {
            margin-top: 10rem;
        }
    .about__me {
            width: 65%;
            margin: 0 auto 3rem;
        }
    
    .about__cards {
           grid-template-columns: 1fr 1fr;
           gap:1rem;
     }

    .about__contnet{
        text-align:center;
    }
    .about__contnet p{
        margin:1.5rem 0;
    }
    
}

/*---------------------MEDIA QUERIES(SMALLER SCREEN DEVICES)===========*/
@media screen and (max-width:400px) {
.responsive {
        margin-top: 10rem;
    }

    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__contnet {
        text-align: center;
    }

    .about__contnet p {
        margin: 1.1rem 0;
    }
}